import request from '@/request/api'
/**===========基本信息接口 */
//获取资产分类的列表
export function getListApi(data = {}) {
    return request({
        url: '/systemmanger/deptAction/newDepartmentQuery.htm',
        method: 'post',
        data: data
    })
}

//获取资产分类的分类树
export function getAssetTypeTree(data = {}) {
    return request({
        url: '/asset/assetTypeAction/assetTypeTree',
        method: 'get'
    })
}




//-查询资产分类上级
export function getTypeSuperList(data = {}) {
    return request({
        url: '/asset/assetTypeAction/assetTypeSuperList',
        method: 'post',
        data: data
    })
}


//--资产分类列表
export function queryTypeList(data = {}) {
    return request({
        url: '/asset/assetTypeAction/queryList',
        method: 'post',
        data: data
    })
}




