<template>
  <div class="standardSupplies h100">
    <el-row class="h100">
      <el-col :span="6" class="pd-r10 h100">
        <usualTree
          @handleClickTree="handleClickTree"
          :haveRemark="true"
          ref="orgTree"
          treeType = "depart"
        ></usualTree>
      </el-col>
      <el-col :span="18" class="h100">
        <KlTable v-bind="tableConfig" ref="klTable"></KlTable>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  getListApi, //获取资产分类的列表
  // getAssetTypeTree, //获取资产分类的分类树
  // getTypeSuperList, //-查询资产分类上级
  // queryTypeList, //--资产分类列表
} from "@/api/systemManage/departmentManage";
import usualTree from "../usualTree";

export default {
  components: { usualTree },
  data() {
    return {
      //物资信息
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-input",
              label: "部门名称",
              model: "name",
              placeholder: "请输入部门名称",
            },
          ],
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            parentid: "",
            page: 1,
            pageSize: 15,
          },
        },
        tableDataObj: {
          tableArr: [],
          total: null,
        },
        operateArr: [
          {
            name: "新建",
            isHide: true,
            icon: "el-icon-plus",
            method: (name) => {
              this.toAdd();
            },
          },
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: (val) => {
              this.selectionData = val;
            },
          },
          // table表展示列配置
          showItems: [
            {
              name: "部门名称",
              prop: "name",
            },
            {
              name: "部门简称",
              prop: "shortname",
            },
            {
              name: "类型",
              prop: "name3",
            },
             {
              name: "等级",
              prop: "name4",
            },
            // {
            //   name: "资产类型",
            //   prop: "typename",
            //   width: 100
            // },
            // {
            //   name: "排序",
            //   prop: "sort"
            // }
          ],
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100],
        },
        paginationIsshow: true,
        showklTable: false,
      },
      //当前选择树
      currentTreeNode: {},
    };
  },
  watch: {
    currentTreeNode(val) {
      // console.log(val);
      let id = val.id || "";
      //获取表格的信息
      this.getTableData(id);
    },
  },
  created() {},
  methods: {
    //点击树
    handleClickTree(node) {
      console.log(node);
      //对选择的数据进行处理
      this.currentTreeNode = Object.assign({}, node);
    },

    //获取资产分类列表信息
    getTableData(id) {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      form.parentid = id;
      form.pageCount = form.pageSize;
      if (form.parentid === undefined) {
        form.parentid = "";
      }
      this.$delete(form, "pageSize");
      this.$delete(form, "parentid");
      getListApi(form)
        .then((res) => {
          // console.log(res.data);
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch((res) => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },
  },
};
</script>

<style lang="scss">
.standardSupplies {
  padding-top: 10px;
  .pd-r10 {
    padding-right: 10px;
  }
}
</style>