<template>
  <div class="lazyTree h100">
    <div class="edit-tree-toolbar">
      <div>{{ title }}</div>
    </div>
    <div class="edit-tree-content">
      <el-tree
        class="filter-tree"
        node-key="id"
        :data="data"
        :props="defaultProps"
        @node-click="handleNodeClick"
      >
      </el-tree>
    </div>
  </div>
</template>
<script>
import {
  getModelTree, //获取菜单树  treeType = menu
  getDepartmentTree //获取部门树  treeType = depart
} from "@/api/common";
export default {
  props: ["treeType"],

  data() {
    return {
      indent: 16,
      title: "组织选择",
      resolve: null,
      btnArr: [],
      currentNode: {}, //当前节点
      show: false,
      remarkItem: {
        label: "备注",
        prop: "remark",
        type: "text",
        span: 24
      },
      defaultProps: {
        children: "children",
        label: ""
      },
      data: []
    };
  },
  created() {
    this.init();
  },
  watch: {},
  methods: {
    currentChange(data, node) {},
    init() {

      //获取树
      if (this.treeType == "menu") {
        this.defaultProps.label="modelName"
        this.getMenuTree(); //获取菜单树
      } else if (this.treeType == "depart") {
        this.defaultProps.label="name"
        this.getDepartTree(); //获取组织树
      }
    },
    //======================
    //点击事件
    handleNodeClick(node, resolve) {
      this.$emit("handleClickTree", node);
    },
    //获取菜单树
    getMenuTree() {
      getModelTree().then(res => {
        this.data = res.data;
      });
    },
    //获取组织树
    getDepartTree() {
      getDepartmentTree().then(res => {
        this.data = res.data;
      });
    },
    
  }
};
</script>

<style lang="scss" scoped>
.lazyTree {
  border: 1px solid #ebeef5;
  min-height: 300px;
  overflow-y: auto;
  overflow-x: auto;
  margin-bottom: 10px;
}

.edit-tree-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
</style>